.layout {
  width: 100%;
  
  .body {
    background-color: map-get($color, "gray-5");
  }

  .footer-1 {
    display: flex;
    flex-direction: column;
    align-items: center;

    .footer-1-links {
      @include fluidpaddingy(2rem, 3.5rem);
      @include fluidpaddingx(1rem);
      @include fluid("border-bottom-width", .1rem);
      border-bottom-style: solid;
      border-bottom-color: map-get($color, "gray-5");
      
      // Scroll Behavior Warning!
      max-width: 100vw; // Important for negate flex content overflow
      overflow-x: hidden;

      @include bp-max-width($bp-medium) {
        overflow-x:scroll;
      }

      a {
        @include fluidpaddingx(2rem);
        color: map-get($color, "primary-gray-1");
        cursor: pointer;
        text-decoration: none;
        font-weight: 600;

        &:hover {
          color: map-get($color, "primary");
        }
      }
    }

    .footer-1-body {
      text-align: center;

      h3 {
        @include fluid("font-size", 1.6rem);
      }

      p {
        @include fluid("font-size", 1.4rem);
      }

      .footer-1-buttons {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  .footer-2 {
    @include fluidpadding(2rem);
    display: flex;
    justify-content: center;
    
    p {
      text-align: center;
      @include fluid("font-size", 1.2rem);
    }
  }

  .footer-3 {
    @include fluidpadding(2rem);
    position: fixed;
    bottom: 0;
    width: 100%;

    .footer-3-container {
      @include box-shadow();
      @include fluidpaddingx(1rem, 2rem);
      @include fluidpaddingy(1rem);
      @include fluid("border-radius", .5rem);
      background-color: white;

      .button {
        text-align: center;
        @include fluid("border-radius", .5rem);
      }

      .footer-3-link {
        color: map-get($color, "gray-1");
        cursor: pointer;

        &:hover {
          color: map-get($color, "primary");
        }

        .footer-3-icon {
          display: flex;
          flex-direction: column;
          align-items: center;

          img {
            @include fluid("height", 3rem, 4rem);
          }
        }
      }
    }
  }
}